@keyframes float {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-20px);
  }
  100% {
    transform: translateY(0px);
  }
}

@keyframes pulse {
  0% {
    transform: scale(1);
    opacity: 0.5;
  }
  50% {
    transform: scale(1.05);
    opacity: 0.8;
  }
  100% {
    transform: scale(1);
    opacity: 0.5;
  }
}

.gradient-text {
  background: linear-gradient(135deg, #8A4FFF 0%, #A375FF 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.card-hover {
  transition: all 0.3s ease;
}

.card-hover:hover {
  transform: translateY(-8px);
  box-shadow: 0 12px 48px rgba(138, 79, 255, 0.2);
}

.button-hover {
  transition: all 0.3s ease;
}

.button-hover:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(138, 79, 255, 0.3);
}

/* Smooth scrolling */
html {
  scroll-behavior: smooth;
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: #8A4FFF;
  border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
  background: #A375FF;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f4f6f8; /* Or your default app background */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Keyframes for subtle background animation */
@keyframes subtleGradientAnimation {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

/* Kaicension Loader Styles */
.kaicension-loader {
  position: relative;
  display: inline-block; 
  text-align: center; 
  margin-bottom: 1.5rem; 
}

.kaicension-loader .kaicension-text { /* Class for the text element */
  position: relative;
  z-index: 2;
  font-size: 3rem;
  font-weight: bold;
  color: #ffffff;
  margin: 0;
  padding: 20px 40px; 
  text-align: center;
  line-height: 1.2;
}

/* Keyframes for subtle background glow */
@keyframes breathingGlow {
  0% { opacity: 0.15; transform: scale(1); }
  50% { opacity: 0.3; transform: scale(1.02); }
  100% { opacity: 0.15; transform: scale(1); }
}

@media (max-width: 600px) {
  .kaicension-loader .kaicension-text {
    font-size: 2.2rem; 
    padding: 15px 30px; 
  }
} 